<template>
  <div class="valuation-info" :class="confidenceClass">
    <el-row type="flex" align="middle" justify="space-between">
      <el-row type="flex" align="middle">
        <EquipmentImage :equipment="targetEquipment" width="50px" height="50px" placement="left" />
        <span v-if="targetEquipment.price" v-html="formatFullPrice(targetEquipment)" style="margin-left: 10px"></span>
        <div v-if="isWeapon" style="margin-left: 10px">
          <div style="margin-bottom: 5px"> <el-tag type="success">Â±ûÊÄßÔºö+{{ valuation.feature.addon_total }}ÁÇπ{{
            addon_total_standards?.[valuation.feature.equip_level] }}</el-tag>
            <el-divider direction="vertical" />
            <el-tag type="primary">ÊÄª‰º§Ôºö{{ valuation.feature.all_damage }}/{{
              parseInt(all_damage_standards?.[valuation.feature.equip_level][1]
                + (25 * valuation.feature.equip_level / 30)) }}</el-tag>
          </div>
          <div>
            <el-tag type="danger">Âàù‰º§Ôºö{{ valuation.feature.init_damage_raw }}ÁÇπ{{
              init_damage_raw_standards?.[valuation.feature.equip_level] }}</el-tag>
            <el-divider direction="vertical" />
            <el-tag type="danger">ÂàùÊÄª‰º§Ôºö{{ valuation.feature.init_damage }}ÁÇπ{{
              all_damage_standards?.[valuation.feature.equip_level] }}</el-tag>
          </div>
        </div>
      </el-row>
      <!-- Âà∑Êñ∞ÂíåÁõ∏‰ººË£ÖÂ§áÁïåÈù¢ -->
      <div style="width: 170px;flex-shrink: 0;">
        <el-button type="primary" @click="$emit('refresh')" size="mini">Âà∑Êñ∞</el-button>
        <SimilarGetMore :target-equipment="targetEquipment" />
      </div>
    </el-row>
    <div class="valuation-main">
      <span class="valuation-label">Ë£ÖÂ§á‰º∞‰ª∑:</span>
      <span class="valuation-price">{{ valuation?.estimated_price_yuan || '-' }}ÂÖÉ</span>
      <span class="valuation-strategy">({{ valuation ? getStrategyName(valuation.strategy) : '-' }})</span>
      <el-link type="danger" @click.native="markAsAbnormal" size="mini">Ê†áËÆ∞‰∏∫ÂºÇÂ∏∏</el-link>

      <!-- ‰ª∑Ê†ºÊØîÁéáÊòæÁ§∫ -->
      <span v-if="priceRatio" class="price-ratio">
        <el-tag :type="priceRatioTagType" disable-transitions>
          {{ priceRatioText }}
        </el-tag>
      </span>
    </div>
    <div class="valuation-details">
      <span class="confidence-display" :class="confidenceTextClass">
        <i :class="confidenceIcon"></i>
        ÁΩÆ‰ø°Â∫¶: {{ valuation ? (valuation.confidence * 100).toFixed(1) + '%' : '-' }}
        <span class="confidence-level">{{ confidenceLevel }}</span>
      </span>
      <span>Âü∫‰∫é{{ valuation.anchor_count }}‰∏™ÈîöÁÇπ</span>
      <span v-if="priceRatio">‰º∞‰ª∑ÊØîÁéá: {{ (priceRatio * 100).toFixed(1) }}%</span>
    </div>
  </div>
</template>

<script>
import EquipmentImage from './EquipmentImage/EquipmentImage.vue'
import { equipmentMixin } from '@/utils/mixins/equipmentMixin'
import { commonMixin } from '@/utils/mixins/commonMixin'
import SimilarGetMore from './SimilarGetMore.vue'

export default {
  name: 'EquipmentValuation',
  components: {
    EquipmentImage,
    SimilarGetMore
  },
  mixins: [equipmentMixin, commonMixin],
  props: {
    valuation: {
      type: Object,
      default: null
    },
    targetEquipment: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      weaponConfig: null,
      addon_total_standards: null,
      all_damage_standards: null,
      init_damage_raw_standards: null,
    }
  },
  computed: {
    isWeapon() {
      return window.is_weapon_equip(this.valuation?.feature?.kindid)
    },
    // ËÆ°ÁÆó‰º∞‰ª∑‰∏éÂîÆ‰ª∑ÁöÑÊØîÁéá
    priceRatio() {
      if (!this.valuation || !this.valuation.estimated_price_yuan || !this.targetEquipment.price) {
        return null
      }

      const estimatedPrice = parseFloat(this.valuation.estimated_price_yuan)
      const sellingPrice = parseFloat(this.targetEquipment.price) / 100 // ËΩ¨Êç¢‰∏∫ÂÖÉ

      if (sellingPrice === 0) return null

      return estimatedPrice / sellingPrice
    },
    priceRatioTagType() {
      if (!this.priceRatio) return ''
      const ratio = this.priceRatio
      const deviation = Math.abs(ratio - 1) * 100
      if (deviation < 5) {
        return 'success'
      } else if (deviation < 10) {
        return 'info'
      } else if (deviation < 20) {
        return 'warning'
      } else {
        return 'danger'
      }
    },
    priceRatioText() {
      if (!this.priceRatio) return ''
      const ratio = this.priceRatio
      const deviation = Math.abs(ratio - 1) * 100
      if (deviation < 5) {
        return `‰º∞‰ª∑ÊûÅ‰∏∫Ë¥¥ÂêàÂ∏ÇÂú∫Ôºà¬±${deviation.toFixed(1)}%Ôºâ`
      } else if (deviation < 10) {
        return `‰º∞‰ª∑ËæÉ‰∏∫Ë¥¥ÂêàÔºà¬±${deviation.toFixed(1)}%Ôºâ`
      } else if (deviation < 20) {
        return `‰º∞‰ª∑Êúâ‰∏ÄÂÆöÂÅèÂ∑ÆÔºà¬±${deviation.toFixed(1)}%Ôºâ`
      } else if (ratio > 1) {
        return `‰º∞‰ª∑È´ò‰∫éÂ∏ÇÂú∫Ôºà+${((ratio - 1) * 100).toFixed(1)}%Ôºâ`
      } else {
        return `‰º∞‰ª∑‰Ωé‰∫éÂ∏ÇÂú∫Ôºà-${((1 - ratio) * 100).toFixed(1)}%Ôºâ`
      }
    },
    // Ê†πÊçÆÁΩÆ‰ø°Â∫¶ËøîÂõûÂØπÂ∫îÁöÑCSSÁ±ª
    confidenceClass() {
      if (!this.valuation || !this.valuation.confidence) {
        return 'confidence-extremely-low'
      }
      
      const confidence = this.valuation.confidence
      
      if (confidence >= 0.8) {
        return 'confidence-high'        // >= 80%: È´òÁΩÆ‰ø°Â∫¶ (ÁªøËâ≤)
      } else if (confidence >= 0.6) {
        return 'confidence-medium'      // 60-79%: ‰∏≠Á≠âÁΩÆ‰ø°Â∫¶ (ËìùËâ≤)
      } else if (confidence >= 0.4) {
        return 'confidence-low'         // 40-59%: ËæÉ‰ΩéÁΩÆ‰ø°Â∫¶ (ÁÅ∞Ëâ≤)
      } else if (confidence >= 0.2) {
        return 'confidence-very-low'    // 20-39%: Âæà‰ΩéÁΩÆ‰ø°Â∫¶ (Ê©ôËâ≤)
      } else {
        return 'confidence-extremely-low' // < 20%: ÊûÅ‰ΩéÁΩÆ‰ø°Â∫¶ (Á∫¢Ëâ≤)
      }
    },
    // ÁΩÆ‰ø°Â∫¶ÊñáÊú¨È¢úËâ≤Á±ª
    confidenceTextClass() {
      if (!this.valuation || !this.valuation.confidence) {
        return 'text-danger'
      }
      
      const confidence = this.valuation.confidence
      
      if (confidence >= 0.8) {
        return 'text-success'
      } else if (confidence >= 0.6) {
        return 'text-primary'
      } else if (confidence >= 0.4) {
        return 'text-info'
      } else if (confidence >= 0.2) {
        return 'text-warning'
      } else {
        return 'text-danger'
      }
    },
    // ÁΩÆ‰ø°Â∫¶ÂõæÊ†á
    confidenceIcon() {
      if (!this.valuation || !this.valuation.confidence) {
        return 'el-icon-warning'
      }
      
      const confidence = this.valuation.confidence
      
      if (confidence >= 0.8) {
        return 'el-icon-success'
      } else if (confidence >= 0.6) {
        return 'el-icon-info'
      } else if (confidence >= 0.4) {
        return 'el-icon-question'
      } else if (confidence >= 0.2) {
        return 'el-icon-warning'
      } else {
        return 'el-icon-error'
      }
    },
    // ÁΩÆ‰ø°Â∫¶Á≠âÁ∫ßÊèèËø∞
    confidenceLevel() {
      if (!this.valuation || !this.valuation.confidence) {
        return '(Êï∞ÊçÆÁº∫Â§±)'
      }
      
      const confidence = this.valuation.confidence
      
      if (confidence >= 0.8) {
        return '(È´ò)'
      } else if (confidence >= 0.6) {
        return '(‰∏≠)'
      } else if (confidence >= 0.4) {
        return '(ÂÅè‰Ωé)'
      } else if (confidence >= 0.2) {
        return '(Âæà‰Ωé)'
      } else {
        return '(ÊûÅ‰Ωé)'
      }
    },
  },
  methods: {
    getWeaponConfig() {
      this.$api.equipment.getWeaponConfig().then(res => {
        if (res.code === 200) {
          this.weaponConfig = res.data
          this.addon_total_standards = res.data.addon_total_standards
          this.all_damage_standards = res.data.all_damage_standards
          this.init_damage_raw_standards = res.data.init_damage_raw_standards
        }
      })
    },
    async markAsAbnormal() {
      try {
        // Ë∞ÉÁî®APIÊ†áËÆ∞Ë£ÖÂ§á‰∏∫ÂºÇÂ∏∏
        const response = await this.$api.equipment.markEquipmentAsAbnormal({
          equipment_data: this.targetEquipment,
          reason: 'Ê†áËÆ∞ÂºÇÂ∏∏',
          notes: 'Áî®Êà∑ÊâãÂä®Ê†áËÆ∞ÁöÑÂºÇÂ∏∏Ë£ÖÂ§á'
        })

        if (response.code === 200) {
          this.$notify.success({
            title: 'ÊèêÁ§∫',
            message: 'Ë£ÖÂ§áÂ∑≤Ê†áËÆ∞‰∏∫ÂºÇÂ∏∏'
          })
          // ÂèØ‰ª•Ëß¶ÂèëÁà∂ÁªÑ‰ª∂Âà∑Êñ∞ÊàñÂÖ∂‰ªñÊìç‰Ωú
          this.$emit('abnormal-marked', this.targetEquipment)
        } else {
          this.$notify.error({
            title: 'ÊèêÁ§∫',
            message: response.message || 'Ê†áËÆ∞ÂºÇÂ∏∏Â§±Ë¥•'
          })
        }
      } catch (error) {
        console.error('Ê†áËÆ∞ÂºÇÂ∏∏Â§±Ë¥•:', error)
        this.$notify.error({
          title: 'ÊèêÁ§∫',
          message: 'Ê†áËÆ∞ÂºÇÂ∏∏Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï'
        })
      }
    },
    getStrategyName(strategy) {
      const strategyNames = {
        fair_value: 'ÂÖ¨ÂÖÅ‰ª∑ÂÄº',
        competitive: 'Á´û‰∫â‰ª∑Ê†º',
        premium: 'Ê∫¢‰ª∑‰º∞ÂÄº'
      }
      return strategyNames[strategy] || strategy
    },
  },
  mounted() {
    if (this.isWeapon) {
      this.getWeaponConfig()
    }
  }
}
</script>

<style scoped>
.valuation-info {
  margin: 12px 0;
  padding: 12px;
  background-color: #f8f9fa;
  border-radius: 6px;
  border-left: 4px solid #409eff;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.valuation-main {
  font-size: 14px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 8px;
}

.valuation-label {
  color: #606266;
  font-weight: 500;
}

.valuation-price {
  color: #e6a23c;
  font-weight: 600;
  font-size: 16px;
}

.valuation-strategy {
  color: #909399;
  font-size: 12px;
}

.valuation-details {
  display: flex;
  gap: 15px;
  font-size: 12px;
  color: #909399;
  flex-wrap: wrap;
}

.confidence-display {
  display: flex;
  align-items: center;
  gap: 4px;
  font-weight: 500;
}

.confidence-display i {
  font-size: 14px;
}

.confidence-level {
  font-size: 11px;
  opacity: 0.8;
}

/* ÁΩÆ‰ø°Â∫¶ÊñáÊú¨È¢úËâ≤ */
.text-success {
  color: #67c23a !important;
}

.text-primary {
  color: #409eff !important;
}

.text-info {
  color: #909399 !important;
}

.text-warning {
  color: #e6a23c !important;
}

.text-danger {
  color: #f56c6c !important;
}
/* Ê†πÊçÆÁΩÆ‰ø°Â∫¶ÁöÑÈ¢úËâ≤ÂèòÂåñ */
.valuation-info.confidence-high {
  border-left: 4px solid #67c23a;
  /* ÁªøËâ≤ - È´òÁΩÆ‰ø°Â∫¶ */
  background: linear-gradient(270deg, #f0f9ff 0%, #e1f3d8 100%);
}

.valuation-info.confidence-medium {
  border-left: 4px solid #409eff;
  /* ËìùËâ≤ - ‰∏≠Á≠âÁΩÆ‰ø°Â∫¶ */
  background: linear-gradient(270deg, #f0f8ff 0%, #e1f5fe 100%);
}

.valuation-info.confidence-low {
  border-left: 4px solid #909399;
  /* ÁÅ∞Ëâ≤ - ËæÉ‰ΩéÁΩÆ‰ø°Â∫¶ */
  background: linear-gradient(270deg, #f8f9fa 0%, #e9ecef 100%);
}

.valuation-info.confidence-very-low {
  border-left: 4px solid #e6a23c;
  /* Ê©ôËâ≤ - Âæà‰ΩéÁΩÆ‰ø°Â∫¶ */
  background: linear-gradient(270deg, #fdf6ec 0%, #fdf2e9 100%);
}

.valuation-info.confidence-extremely-low {
  border-left: 4px solid #f56c6c;
  /* Á∫¢Ëâ≤ - ÊûÅ‰ΩéÁΩÆ‰ø°Â∫¶ */
  background: linear-gradient(270deg, #fef0f0 0%, #fde2e2 100%);
}
</style>
