<template>
  <el-popover :data-equip-sn="equipment.equip_sn" :placement="placement" width="720" trigger="click"
    popper-class="similar-equip-popper" v-model="visible" @show="handleShow">
    <template #reference>
      <slot>
        <el-link type="primary" style="font-size: 12px;">Êü•ÁúãÁõ∏‰ºº</el-link>
      </slot>
    </template>

    <!-- Áõ∏‰ººË£ÖÂ§áÂÜÖÂÆπ -->
    <div v-if="visible">
      <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
      <div v-if="valuationLoading" class="loading-info">
        <el-skeleton :rows="12" animated />
      </div>
      <div v-else-if="similarData">
        <div class="similar-header">
          <h4>Áõ∏‰ººË£ÖÂ§á (ÂÖ±{{ similarData.anchor_count }}‰∏™) <el-divider direction="vertical" />
            <el-tag type="info" size="mini">Áõ∏‰ººÂ∫¶ÈòàÂÄº: {{ similarData.similarity_threshold }}</el-tag>
            <el-divider direction="vertical" />
            <el-tag type="info" size="mini">ÊúÄÂ§ßÈîöÁÇπÊï∞: {{ similarData.max_anchors }}</el-tag>
          </h4>
          <!-- Ë£ÖÂ§á‰º∞‰ª∑‰ø°ÊÅØ -->
          <EquipmentValuation :valuation="equipmentValuation" :target-equipment="getEquipImageProps(equipment)"
            @refresh="refresh" />
          <div v-if="similarData.statistics" class="stats">
            <span>
              ‰ª∑Ê†ºËåÉÂõ¥:
              <span v-html="formatPrice(similarData.statistics.price_range.min)"></span>
              -
              <span v-html="formatPrice(similarData.statistics.price_range.max)"></span>
            </span>
            <span> Âπ≥ÂùáÁõ∏‰ººÂ∫¶: {{ similarData.statistics.similarity_range.avg.toFixed(3) }} </span>
          </div>
        </div>

        <!-- Áõ∏‰ººË£ÖÂ§áË°®Ê†º -->
        <el-empty v-if="!anchorsLoading && !similarData?.anchors?.length" description="ÊöÇÊó†Êï∞ÊçÆ"></el-empty>
        <SimilarEquipmentTable v-else :anchors="similarData.anchors" v-loading="anchorsLoading" element-loading-text="Ê≠£Âú®Âä†ËΩΩÁõ∏‰ººË£ÖÂ§á"/>
      </div>
    </div>
  </el-popover>
</template>

<script>
import EquipmentValuation from './EquipmentValuation.vue'
import SimilarEquipmentTable from './SimilarEquipmentTable.vue'
import { equipmentMixin } from '@/utils/mixins/equipmentMixin'
import { commonMixin } from '@/utils/mixins/commonMixin'

export default {
  name: 'SimilarEquipmentModal',
  components: {
    EquipmentValuation,
    SimilarEquipmentTable
  },
  mixins: [equipmentMixin, commonMixin],
  props: {
    equipment: {
      type: Object,
      required: true
    },
    placement: {
      type: String,
      default: 'left-end'
    },
    similarityThreshold: {
      type: Number,
      default: 0.8
    },
    maxAnchors: {
      type: Number,
      default: 30
    },
  },
  data() {
    return {
      visible: false,
      valuationLoading: false,  // Ë£ÖÂ§á‰º∞‰ª∑Êé•Âè£Âä†ËΩΩÁä∂ÊÄÅ
      anchorsLoading: false,    // Áõ∏‰ººË£ÖÂ§áÈîöÁÇπÊé•Âè£Âä†ËΩΩÁä∂ÊÄÅ
      error: false,
      similarData: null,
      equipmentValuation: {}
    }
  },
  computed: {
    // Êï¥‰ΩìÂä†ËΩΩÁä∂ÊÄÅ
    loading() {
      return this.valuationLoading || this.anchorsLoading
    }
  },
  methods: {
    async handleShow() {
      if (!this.similarData) {
        await this.loadSimilarEquipments()
      }
    },

    async refresh() {
      await this.loadSimilarEquipments()
    },

    // Âä†ËΩΩÁõ∏‰ººË£ÖÂ§á
    async loadSimilarEquipments() {
      this.error = false
      this.equipmentValuation = {}
      this.similarData = null

      try {
        await this.loadEquipmentValuation(this.equipment, this.similarityThreshold)
      } catch (error) {
        console.error('Âä†ËΩΩÁõ∏‰ººË£ÖÂ§áÂ§±Ë¥•:', error)
        this.error = true
      }
    },

    // Áªü‰∏ÄÁöÑË£ÖÂ§á‰º∞‰ª∑Âä†ËΩΩÊñπÊ≥ï
    async loadEquipmentValuation(equipment, similarityThreshold) {
      try {
        // Á¨¨‰∏Ä‰∏™Êé•Âè£ÔºöËé∑ÂèñË£ÖÂ§á‰º∞‰ª∑‰ø°ÊÅØ
        this.valuationLoading = true
        if (window.is_pet_equip(equipment.kindid)) {
          equipment = {
            kindid: 29,
            desc: equipment.desc
          }
        }
        const response = await this.$api.equipment.getEquipmentValuation({
          equipment_data: equipment,
          strategy: 'fair_value',
          similarity_threshold: similarityThreshold,
          max_anchors: this.maxAnchors
        })

        const result = response.data
        this.equipmentValuation = result

        // ÂêëÁà∂ÁªÑ‰ª∂ÂèëÂá∫‰º∞‰ª∑ÁªìÊûúÊõ¥Êñ∞‰∫ã‰ª∂
        this.$emit('valuation-updated', result)

        // ÂàùÂßãÂåñÁõ∏‰ººË£ÖÂ§áÊï∞ÊçÆ
        this.similarData = {
          anchor_count: result.anchor_count,
          similarity_threshold: result.similarity_threshold || similarityThreshold,
          max_anchors: result.max_anchors || this.maxAnchors,
          anchors: [],
          statistics: {
            price_range: result.anchor_count?{
              min: Math.min(...result.anchors.map((a) => a.price || 0)),
              max: Math.max(...result.anchors.map((a) => a.price || 0)),
              avg: result.anchors.reduce((sum, a) => sum + (a.price || 0), 0) / result.anchors.length
            }:{
              min:0,
              max:0,
              avg:0
            },
            similarity_range: result.anchor_count?{
              min: Math.min(...result.anchors.map((a) => a.similarity || 0)),
              max: Math.max(...result.anchors.map((a) => a.similarity || 0)),
              avg: result.anchors.reduce((sum, a) => sum + (a.similarity || 0), 0) / result.anchors.length
            }:{
              min:0,
              max:0,
              avg:0
            }
          }
        }

        this.valuationLoading = false

        // Â§ÑÁêÜ‰º∞‰ª∑ÂìçÂ∫îÔºåÂ¶ÇÊûúÊúâÈîöÁÇπÊï∞ÊçÆÂàôÂä†ËΩΩËØ¶ÁªÜ‰ø°ÊÅØ
        if (result?.anchor_count > 0 && result?.anchors?.length > 0) {
          // Á¨¨‰∫å‰∏™Êé•Âè£ÔºöËé∑ÂèñÁõ∏‰ººË£ÖÂ§áÈîöÁÇπËØ¶ÁªÜÊï∞ÊçÆ
          this.anchorsLoading = true

          try {
            // ‰ΩøÁî®‰º∞‰ª∑ÁªìÊûú‰∏≠ÁöÑequip_sn_listÁõ¥Êé•Ëé∑ÂèñÁõ∏‰ººË£ÖÂ§áÂàóË°®ÔºåÈÅøÂÖçÈáçÂ§çËÆ°ÁÆó
            const anchorsResponse = await this.$api.equipment.getEquipmentList({
              page_size: 99,
              equip_sn_list: result.anchors.map(item => item.equip_sn)
            })

            this.anchorsLoading = false
            // ÂêàÂπ∂Áõ∏‰ººÂ∫¶ÂíåÊï∞ÊçÆ
            if (anchorsResponse.code === 200 && anchorsResponse.data?.data) {
              const anchorsData = anchorsResponse.data.data
              const parsedAnchors = anchorsData.map((item, index) => {
                // Ê∑ªÂä†Áõ∏‰ººÂ∫¶‰ø°ÊÅØ
                item.similarity = result.anchors[index].similarity
                item.features = result.anchors[index].features
                return item
              })

              // Êõ¥Êñ∞Áõ∏‰ººË£ÖÂ§áÊï∞ÊçÆ
              this.$set(this.similarData, 'anchors', parsedAnchors)
            } else {
              console.warn('Êú™Ëé∑ÂèñÂà∞Áõ∏‰ººË£ÖÂ§áÈîöÁÇπÊï∞ÊçÆ:', anchorsResponse.message)
            }
          } catch (error) {
            console.error('Êü•ËØ¢Áõ∏‰ººË£ÖÂ§áÈîöÁÇπÂ§±Ë¥•:', error)
            // ÈîöÁÇπÊü•ËØ¢Â§±Ë¥•‰∏çÂΩ±Âìç‰º∞‰ª∑ÁªìÊûúÊòæÁ§∫
          }
        }

      } catch (error) {
        console.error('Ë£ÖÂ§á‰º∞‰ª∑Â§±Ë¥•:', error)
        this.$notify.error({
          title: '‰º∞‰ª∑ËØ∑Ê±ÇÂ§±Ë¥•',
          message: 'ÁΩëÁªúËØ∑Ê±ÇÂºÇÂ∏∏ÔºåËØ∑Á®çÂêéÈáçËØï',
          duration: 3000
        })
        throw error
      } finally {
        // Á°Æ‰øùÂú®Âá∫Áé∞ÂºÇÂ∏∏Êó∂‰πüÈáçÁΩÆÂä†ËΩΩÁä∂ÊÄÅ
        this.valuationLoading = false
        this.anchorsLoading = false
      }
    }
  }
}
</script>

<style scoped>
.similar-header {
  margin-bottom: 16px;
  padding-bottom: 12px;
  border-bottom: 1px solid #ebeef5;
}

.similar-header h4 {
  margin: 0 0 8px 0;
  color: #303133;
  font-size: 16px;
}

.similar-header p {
  margin: 0 0 8px 0;
  color: #606266;
  font-size: 14px;
}

.stats {
  display: flex;
  gap: 20px;
  font-size: 12px;
  color: #909399;
}

.error-info {
  padding: 20px 0;
}

.loading-info {
  padding: 20px;
}

.loading-status {
  margin-bottom: 16px;
}

.loading-item {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
  color: #409eff;
  font-size: 14px;
}

.loading-item i {
  margin-right: 8px;
  animation: rotating 2s linear infinite;
}

@keyframes rotating {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
</style>
